@import '../styles/settings/variables';

article.realizations {
  margin-bottom: 20px;
  margin-top: -30px;

  .row {
    justify-content: center;
    align-items: center;

    .realizations__title {
      margin-bottom: 20px;
      font-size: 30px;
      line-height: 36px;
      font-weight: 300;
      color: $colorTextB;
    }

    .realizations__box {
      flex: 1 1 50%;
      height: 300px;
      margin: 15px;
      border-radius: 12px;
      box-shadow: 1px 1px 10px 3px $colorText;
      overflow: hidden;
      transition: flex 500ms;
      position: relative;

      .fas {
        color: $colorTextW;
        font-size: 30px;
        line-height: 50px;
        background-color: rgba(0, 0, 0, .5);
        width: 50px;
        height: 50px;
        border-radius: 50%;
        text-align: center;
        display: none;
        position: absolute;
        left: calc(50% - 25px);
        top: calc(50% - 25px);
      }

      &:hover {
        .fas {
          display: block;
        }
      }

      .ReactGridGallery {
        .ReactGridGallery_tile {
          width: 100%;
          height: 300px;

          .ReactGridGallery_tile-viewport {
            width: auto !important;
            height: 300px !important;
            display: flex;
            justify-content: center;
            align-items: center;

            img {
              margin-left: 0 !important;
              width: auto !important;
              height: auto !important;
              transform: scale(1.2);
              transition: transform 500ms;
              display: flex;
              justify-content: center;
              align-items: center;

              &:hover {
                transition: transform 500ms;
                transform: scale(1.1);

                &::before {
                  content: 'f002';
                  display: block;
                }
              }
            }
          }
        }
      }
    }

    &:nth-of-type(3) {
      .realizations__box {

        &:first-of-type {
          flex: 0 1 100%;
        }

      }
    }

    &:nth-of-type(4) {
      .realizations__box {

        &:last-of-type {
          flex: 0 1 100%;
        }

      }
    }
  }
}

@media (max-width: 760px) {
  article.realizations {

    .row {
      flex-direction: column;

      .realizations__box {
        flex: none !important;
        width: 90%;
      }

    }
  }
}